import React from "react";
import axios from "axios";
import { Modal, Button } from "react-bootstrap";

class Signup extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            firstname: "",
            lastname: "",
            email: "",
            password: "",
            birthdate: "",
            gender: "",
            error: "",
            terms: false,
            success: false,
            pending: false,
            passwordConfirm: "",
            termsModal: false
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        if (this.state.terms === false) {
            this.setState({error:"Must accept terms and conditions"})
            return
        }
        if (this.state.password !== this.state.passwordConfirm) {
            this.setState({error:"Passwords do not match"});
            return
        }
        this.setState({pending: true})
        axios.post("https://roommates40plus.com/api/auth/signup", this.state)
        .then(res => {
            this.setState({
                success: true,
                pending: false,
                password: ""
            })
        }).catch(err => {
            this.setState({
                error: err.response.data.message,
                pending: false
            })
        })
    }

    handleChange(event) {
        const target = event.target;
        this.setState({
            [target.name]: target.value,
            error: "",
            success: false
        })
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit} style={{ padding: "10px 0" }}>
                {
                    this.state.success ? 
                    <div className="alert alert-success">
                        <strong>Success:</strong> Confirmation email has been sent to {this.state.email}
                    </div> : null
                }
                {
                    this.state.error ?
                    <div className="alert alert-danger">
                        {this.state.error}
                    </div> : null
                }
                <div className="form-group">
                    <input required pattern="[a-zA-Z]+" type="text" name="firstname" className="form-control" value={this.state.firstname} onChange={this.handleChange} placeholder="First name" />
                </div>
                <div className="form-group">
                    <input required pattern="[a-zA-Z]+" type="text" name="lastname" className="form-control" value={this.state.lastname} onChange={this.handleChange} placeholder="Last name" />
                </div>
                <div className="form-group">
                    <input required type="email" name="email" className="form-control" value={this.state.email} onChange={this.handleChange} placeholder="Email" />
                </div>
                <div className="form-group">
                    <input minLength="8" name="password" required type="password" autoComplete="new-password" className="form-control" value={this.state.password} onChange={this.handleChange} placeholder="Password" />
                    <small className="form-text text-muted">
                        Your password must be 8 or more characters long
                    </small>
                </div>
                <div className="form-group">
                    <input minLength="8" name="passwordConfirm" required type="password" autoComplete="new-password" className="form-control" value={this.state.passwordConfirm} onChange={this.handleChange} placeholder="Confirm Password" />
                </div>
                <div className="form-group">
                    <label>Birthday:</label>
                    <input required type="date" name="birthdate" className="form-control" value={this.state.birthdate} onChange={this.handleChange} />
                </div>
                <div className="form-group ">
                    <label className="radio-inline"><input required type="radio" name="gender" onClick={this.handleChange} value="male" />Male</label>
                    <label className="radio-inline"><input required type="radio" name="gender" onClick={this.handleChange} value="female" />Female</label>
                </div>
                <div className="checkbox">
                    <label><input required type="checkbox" checked={this.state.terms} />I agree to the <a onClick={() => this.setState({termsModal: true})}>Terms and Conditions</a></label>
                </div>
                <button type="submit" disabled={this.state.pending} className="btn btn-default">{this.state.pending ? <i class="fa fa-spinner fa-spin"></i>:null} Sign Up</button>
                <Modal show={this.state.termsModal} onHide={() => this.setState({termsModal:false})}>
                    <Modal.Header closeButton>
                        <Modal.Title>Terms and Conditions</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <p>These are the terms and conditions.</p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button onClick={() => this.setState({terms:false, termsModal:false})}>Decline</Button>
                        <Button bsStyle="primary" onClick={() => this.setState({terms:true, termsModal:false})}>Accept</Button>
                    </Modal.Footer>
                </Modal>
            </form>
        )
    }
}

export default Signup;


