import React from "react";
import { FormControl, Alert } from "react-bootstrap";
import axios from "axios";
import { DOMAIN } from "../constants";
import { Link } from "react-router-dom";

class Match extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            matches: [],
            loading: false,
            error: "",
            matchRequest: {
                state: "AL",
                city: "",
                zip: "",
                budgetMax: "",
                budgetMin: "",
                bedrooms: "",
                bathrooms: "",
                targetResidentCount: "",
                petsAllowed: false,
                smokingAllowed: false
            }
        }
        this.requestMatch = this.requestMatch.bind(this);
        this.matchForm = this.matchForm.bind(this);
        this.matchResults = this.matchResults.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.clearMatchRequest = this.clearMatchRequest.bind(this);
    }

    requestMatch(event) {
        event.preventDefault();
        this.setState({ loading: true });
        let data = {
            ...this.state.matchRequest,
            budgetMax: parseInt(this.state.matchRequest.budgetMax, 10),
            budgetMin: parseInt(this.state.matchRequest.budgetMin, 10),
            zip: parseInt(this.state.matchRequest.zip, 10),
            bedrooms: this.state.matchRequest.bedrooms ? parseInt(this.state.matchRequest.bedrooms, 10) : 0,
            bathrooms: this.state.matchRequest.bathrooms ? parseInt(this.state.matchRequest.bathrooms, 10) : 0,
            preChosenProperty: (this.state.matchRequest.preChosenProperty === "true"),
            targetResidentCount: parseInt(this.state.matchRequest.targetResidentCount, 10),
        }
        if (data.budgetMin >= data.budgetMax) {
            this.setState({
                error: "Max budget can't be higher than min budget",
                loading: false
            })
            return
        }
        const instance = axios.create({
            headers: { 'Authorization': 'Bearer ' + this.props.token }
        })
        instance.post(DOMAIN + "/api/match", data)
            .then(res => {
                if (res.data.length === 0) {
                    this.setState({
                        loading: false,
                        error: "No offers match your search"
                    })
                } else {
                    this.setState({
                        matches: res.data,
                        loading: false
                    })
                    console.log(res.data)
                }
            }).catch(err => {
                this.setState({
                    error: err.response.data.message,
                    loading: false
                })
            })
    }

    clearMatchRequest() {
        this.setState({
            matchRequest: {
                state: "AL",
                city: "",
                zip: "",
                budgetMax: "",
                budgetMin: "",
                bedrooms: "",
                bathrooms: "",
                targetResidentCount: "",
                petsAllowed: false,
                smokingAllowed: false
            }
        })
    }

    handleChange(event) {
        const target = event.target;
        this.setState({
            matchRequest: {
                ...this.state.matchRequest,
                [target.name]: target.value
            },
            error: ""
        })
    }

    matchForm() {
        return (
            <div>
                <div className="page-header">
                    <h1>Match Request <span style={{ color: "darkgray", cursor: "pointer" }} onClick={this.clearMatchRequest} className="glyphicon glyphicon-refresh" /></h1>
                    <p style={{ color: "gray" }}>Here you can enter search criteria and begin looking for roommates. Not all the fields are required, but providing more information will help narrow your results.</p>
                </div>
                {
                    this.state.error ?
                        <Alert bsStyle="danger" onDismiss={() => this.setState({error:""})}>
                            {this.state.error}
                        </Alert> : null
                }
                <form onSubmit={this.requestMatch}>
                    <div className="row">
                        <div className="col-sm-8">
                            <div className="row">
                                <div className="col-sm-12">
                                    <h4>Seeking Location:</h4>
                                    <hr />
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group">
                                        <FormControl name="state" value={this.state.matchRequest.state} onChange={this.handleChange} componentClass="select" placeholder="select">
                                            <option value="AL">Alabama</option>
                                            <option value="AK">Alaska</option>
                                            <option value="AZ">Arizona</option>
                                            <option value="AR">Arkansas</option>
                                            <option value="CA">California</option>
                                            <option value="CO">Colorado</option>
                                            <option value="CT">Connecticut</option>
                                            <option value="DE">Delaware</option>
                                            <option value="DC">District Of Columbia</option>
                                            <option value="FL">Florida</option>
                                            <option value="GA">Georgia</option>
                                            <option value="HI">Hawaii</option>
                                            <option value="ID">Idaho</option>
                                            <option value="IL">Illinois</option>
                                            <option value="IN">Indiana</option>
                                            <option value="IA">Iowa</option>
                                            <option value="KS">Kansas</option>
                                            <option value="KY">Kentucky</option>
                                            <option value="LA">Louisiana</option>
                                            <option value="ME">Maine</option>
                                            <option value="MD">Maryland</option>
                                            <option value="MA">Massachusetts</option>
                                            <option value="MI">Michigan</option>
                                            <option value="MN">Minnesota</option>
                                            <option value="MS">Mississippi</option>
                                            <option value="MO">Missouri</option>
                                            <option value="MT">Montana</option>
                                            <option value="NE">Nebraska</option>
                                            <option value="NV">Nevada</option>
                                            <option value="NH">New Hampshire</option>
                                            <option value="NJ">New Jersey</option>
                                            <option value="NM">New Mexico</option>
                                            <option value="NY">New York</option>
                                            <option value="NC">North Carolina</option>
                                            <option value="ND">North Dakota</option>
                                            <option value="OH">Ohio</option>
                                            <option value="OK">Oklahoma</option>
                                            <option value="OR">Oregon</option>
                                            <option value="PA">Pennsylvania</option>
                                            <option value="RI">Rhode Island</option>
                                            <option value="SC">South Carolina</option>
                                            <option value="SD">South Dakota</option>
                                            <option value="TN">Tennessee</option>
                                            <option value="TX">Texas</option>
                                            <option value="UT">Utah</option>
                                            <option value="VT">Vermont</option>
                                            <option value="VA">Virginia</option>
                                            <option value="WA">Washington</option>
                                            <option value="WV">West Virginia</option>
                                            <option value="WI">Wisconsin</option>
                                            <option value="WY">Wyoming</option>
                                        </FormControl>
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group ">
                                        <input required pattern="^[a-zA-Z\s]*$" type="text" name="city" className="form-control" value={this.state.matchRequest.city} onChange={this.handleChange} placeholder="City" />
                                    </div>
                                </div>
                                <div className="col-sm-4">
                                    <div className="form-group">
                                        <input required type="number" name="zip" className="form-control" value={this.state.matchRequest.zip} onChange={this.handleChange} placeholder="Zip" />
                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <h4>Budget Range:</h4>
                                    <hr />
                                </div>
                                <div className="col-sm-5">
                                    <div className="form-group input-group">
                                        <span className="input-group-addon">$</span>
                                        <input name="budgetMin" required type="number" min="1.00" step="1.00" className="form-control" value={this.state.matchRequest.budgetMin} onChange={this.handleChange} placeholder="0.00" />
                                    </div>
                                </div>
                                <div className="col-sm-1" style={{ color: "gray", fontSize: "20px", padding: "4px", textAlign:"center" }}>
                                    to
                                </div>
                                <div className="col-sm-5">
                                    <div className="form-group input-group">
                                        <span className="input-group-addon">$</span>
                                        <input name="budgetMax" required type="number" min="1.00" step="1.00" className="form-control" value={this.state.matchRequest.budgetMax} onChange={this.handleChange} placeholder="0.00" />
                                    </div>
                                </div>
                                <div className="col-sm-7">
                                    <h4>Target Resident Number (Include yourself):</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="targetResidentCount" required type="number" min="2" step="1" className="form-control" value={this.state.matchRequest.targetResidentCount} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-5">
                                    <h4>Gender Requirement:</h4>
                                    <hr />
                                    <div className="form-group ">
                                        <label className="radio-inline"><input required type="radio" name="genderRequirement" onClick={this.handleChange} value="none" />Doesn't matter</label>
                                        <label className="radio-inline"><input required type="radio" name="genderRequirement" onClick={this.handleChange} value={this.props.user.gender} />Same as me</label>
                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <h4>Ownership Status:</h4>
                                    <hr />
                                    <div className="form-group ">
                                        <label className="radio-inline"><input required type="radio" name="preChosenProperty" onClick={this.handleChange} value={true} />Property Already Owned</label>
                                        <label className="radio-inline"><input required type="radio" name="preChosenProperty" onClick={this.handleChange} value={false} />Property Not Already Owned</label>
                                    </div>
                                </div>
                                <div className="col-sm-12">
                                    <div className="page-header">
                                        <h1>Additional Information</h1>
                                        <p style={{ color: "gray" }}>Not Required</p>
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Bedrooms:</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="bedrooms" type="number" step="1" className="form-control" value={this.state.matchRequest.bedrooms} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <h4>Bathrooms:</h4>
                                    <hr />
                                    <div className="form-group">
                                        <input name="bathrooms" type="number" step="1" className="form-control" value={this.state.matchRequest.bathrooms} onChange={this.handleChange} placeholder="0" />
                                    </div>
                                </div>
                                <div className="col-sm-6" style={{ paddingBottom: "10px" }}>
                                    <h4>Property Type:</h4>
                                    <hr />
                                    <div className="form-group">
                                        <label className="radio-inline"><input type="radio" name="propertyType" onClick={this.handleChange} value="house" />house</label>
                                        <label className="radio-inline"><input type="radio" name="propertyType" onClick={this.handleChange} value="apartment" />apartment</label>
                                    </div>
                                </div>
                                <div className="col-sm-6" style={{ paddingBottom: "10px" }}>
                                    <h4>Extra Conditions:</h4>
                                    <hr />
                                    <div className="checkbox">
                                        <label><input type="checkbox" checked={this.state.matchRequest.smokingAllowed} onChange={event => {
                                            this.setState({ matchRequest: { ...this.state.matchRequest, smokingAllowed: event.target.checked } });
                                        }} />Allows Smoking</label>
                                    </div>
                                    <div className="checkbox">
                                        <label><input type="checkbox" checked={this.state.matchRequest.petsAllowed} onChange={event => {
                                            this.setState({ matchRequest: { ...this.state.matchRequest, petsAllowed: event.target.checked } });
                                        }} />Allows Pets</label>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-4">
                            <h4 style={{ textAlign: "center" }}>Click to begin searching</h4>
                            <hr />
                            <button type="submit" style={{ margin: "10px 0" }} className="btn btn-default btn-primary btn-block btn-lg">Match</button>
                        </div>
                    </div>
                </form>
            </div>
        )
    }

    matchResults() {
        console.log(this.state.matches)
        let matches = this.state.matches.map((match, index) => (
            <tr key={match.ID}>
                <td><Link to={"/user/" + match.uploaderID}>View Offer</Link></td>
                <td>{match.targetResidentCount - match.acceptedResidentCount}</td>
                <td>${match.budget}.00</td>
                <td>{match.city + " " + match.state + ", " + match.zip}</td>
                <td>{match.propertyType}</td>
                <td>{match.bedrooms}</td>
                <td>{match.bathrooms}</td>
            </tr>
        ))
        return (
            <div>
                <div style={{display:"flex", justifyContent:"flex-end"}}>
                    <button className="btn btn-default btn-primary" onClick={() => this.setState({matches:[]})}>
                        Edit Search Options
                    </button>
                </div>
                <div style={{ textAlign: "center", borderRadius:"3px", margin: "4px 0", backgroundColor: "#3055AF", color: "white" }}>
                    <h4 style={{ padding: "10px" }}>Matched Offers</h4>
                </div>
                <div className="table-responsive" style={{marginTop:"4px"}}>
                    <table className="table">
                        <thead style={{color:"#555555"}}>
                            <tr>
                                <th>Details</th>
                                <th>Spots Left</th>
                                <th>Cost per Month</th>
                                <th>Location</th>
                                <th>Property Type</th>
                                <th>Bathrooms</th>
                                <th>Bedrooms</th>
                            </tr>
                        </thead>
                        <tbody>
                            {matches}
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }

    render() {
        return (
            <div className="container" style={{ marginTop: "62px" }}>
                {
                    this.state.loading ?
                        <div className="loader" /> :
                        this.state.matches.length === 0 ?
                            this.matchForm() : this.matchResults()
                }
            </div>
        )
    }
}

export default Match;


